body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.wrapper{
   background-color: rgba(248, 175, 116, 0.781); 
   background-size: cover;
   /* background-color: rgb(27,3,34); */
   left:0;
   top:0;
   min-height: 100vh;  
}

.contentWrapper{ 
    /* background-color: rgb(240, 219, 233); */
    min-height: calc(100vh - 75px);
    margin: 0 auto;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 20px;
    padding-top: 20px;

}

.grids{
    width: 50%
}

.description{
    font: 18px arial, sans-serif;
}

.company{
    font-size: 28px!important;
}

.projects .description{
    font-size: 19px;
    line-height: 1.3;
}

#skill-label{
    margin: 10px!important;
}

.ui.header .ui.label:hover{
    background-color: rgb(236, 250, 154)!important;
    cursor: pointer;
}

h2.ui.header {
    font-size: 1.914rem;
}

h2.ui.header .sub.header {
    font-size: 1.54rem
}

.ui.header .sub.header {
    line-height: 2.99rem;
}

.ui.cards {
    flex-wrap: nowrap;
    line-height: 2.4285em;
    font-size: 20px;
}

#social {
    float: right;
    text-align:center;
    z-index: 9000;
    padding: 12px 15px 0 0;
    margin-bottom: 20px;
}

#social svg{
    padding: 0 5px 5px 0;
    margin-right: 25px;
}

.content  p {
    font-size: 25px! important;
}

#navbar {
    width:'100%';
    text-align:'center';
    max-height: 60px;
    color:'white';
    position:'fixed';
    z-index:'9000';
    border-bottom: '1px solid white';
    background-color: white;
}

#list {
    display:inline-block;
    margin-left: 177px;
}

#list li {
    display: inline-block;
    list-style: none;
    padding: 8px 25px;
    font-size: 25px;
    cursor: pointer;
}

#list li:hover {
    border: black 1px;
}

#two {
    width: 75%
}

#sec {
    display: flex;
    justify-content: center;
}

